@font-face {
  font-family: "Roboto";
  font-weight: 100;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Thin"), url("../fonts/Roboto-Thin.woff2") format("woff2"), url("../fonts/Roboto-Thin.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Regular"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Medium"), url("../fonts/Roboto-Medium.woff") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Bold"), url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.list {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin: 0 auto;
  min-width: 1200px;
  color: #000000;
  display: flex;
  gap: 50px;
  justify-content: center;
  align-items: center;
  padding: 50px 100px;
}
.list__item {
  width: 240px;
  height: 250px;
  padding: 25px;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  background-color: #FFFFFF;
  color: #8d8d8d;
}
.list__item--background-green {
  background-color: #61c98b;
  color: #FFFFFF;
}
.list__wrap {
  display: flex;
  justify-content: space-between;
}
.list__image-holder--background {
  height: 65px;
  width: 65px;
  border-radius: 50%;
  background-color: rgba(88, 165, 119, 0.6);
}
.list__image-holder--background:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
.list__image-holder--background:hover svg path {
  fill: #87CEFA;
}
.list__image-holder svg {
  position: relative;
  top: 16px;
  left: 18px;
  transition: all 0.2s ease-in-out;
}
.list__image-holder svg:hover {
  transform: rotate(360deg) scale(1);
}
.list__title {
  font-size: 36px;
  font-weight: thin;
  margin: 10px 0;
}
.list__item:nth-child(2) .list__image-holder:hover svg path {
  fill: #61c98b;
}
.holder__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 20px;
  line-height: 22px;
  font-weight: bold;
  text-align: right;
  letter-spacing: 0.05em;
}
.holder__list {
  list-style: none;
  padding: 0;
  text-align: right;
  text-transform: capitalize;
  font-size: 16px;
}
.holder__label {
  display: flex;
  justify-content: right;
  position: relative;
}
.holder__checkmark {
  height: 13px;
  width: 13px;
  border: 1.5px solid #FFFFFF;
  border-radius: 50%;
  position: relative;
  margin-top: 2px;
  margin-left: 15px;
}
.holder__input--custome {
  position: relative;
}
.holder__input--custome:checked ~ .holder__checkmark::before {
  content: "";
  height: 8px;
  width: 5px;
  background-color: #61c98b;
  position: absolute;
  right: 0;
  transform: rotate(45deg);
}
.holder__input--custome:checked ~ .holder__checkmark::after {
  content: "";
  height: 3px;
  width: 11px;
  border-bottom: 1.5px solid #FFFFFF;
  border-left: 1.5px solid #FFFFFF;
  position: absolute;
  transform: rotate(-45deg);
  left: 2px;
  top: 0;
}
.holder__input--custome:hover ~ .holder__checkmark::after {
  border-bottom: 1.5px solid rgba(88, 165, 119, 0.6);
  border-left: 1.5px solid rgba(88, 165, 119, 0.6);
}
.holder__input--custome:focus ~ .holder__checkmark {
  outline: 1.8px solid #8d8d8d;
}
.holder__span {
  font-weight: 100;
  text-align: right;
}
.holder__input--checkbox-grey ~ .holder__checkmark {
  position: relative;
  right: 0;
  top: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1.5px solid #8d8d8d;
  text-align: right;
}
.holder__input--checkbox-grey:checked ~ .holder__checkmark {
  border: 1.5px solid #87CEFA;
  background-color: rgba(135, 206, 250, 0.3);
}
.holder__input--checkbox-grey:hover ~ .holder__checkmark {
  border: 1.5px solid #87CEFA;
}
.holder__input--checkbox-grey:focus ~ .holder__checkmark {
  border: 1.5px solid #61c98b;
}
.holder__list--margin {
  margin-bottom: 40px;
}
.holder__item {
  margin-bottom: 5px;
}
.holder__text {
  font-weight: 100;
  text-align: right;
  margin: 0;
}
.holder__text span {
  text-transform: uppercase;
}
